.calendar {
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  padding: 10px;
  width: 7 * ($width-day + $gap-day);
  gap: $gap-day;

  .calendar__day {
    display: flex;
    box-sizing: border-box;
    position: relative;
    width: $width-day;
    height: 100px;
    text-align: center;
    align-items: center;
    justify-content: center;

    border: 1px solid #000;
    background-color: #eee;

    transition: all 0.5s;

    &:hover {
      transform: translateY(-20px);
      background-color: #FFBFCB ;
      cursor: pointer;
    }

    @for $i from 1 through 31 {
      &:nth-child(#{$i}):before{
        content : '#{$i}';
        position: absolute;
      }
    }
  }

  @each $days, $place in $week-day {
    &--start-day-#{$days} &__day:first-child {
      margin-left: calc(#{($width-day + $gap-day)} * #{$place});
    }
  }

  @for $j from 28 through 31 {
    &--month-length-#{$j} &__day:nth-child(n + #{$j + 1}) {
      display: none;
    }
  }
}
